/* ========================
  FOOTER
========================== */
footer{
	.logged-in &{
		display: none;
	}
	& > div{
		background: $lightGrey;
		padding: 10px $siteMarginMobile;
		min-height: 60px;
		@include safeFlex(column, nowrap, center, start);
		@include tab{
			min-height: 80px;
		}
		@include desk{
			padding: 10px $siteMarginDesk;
		}
	}
}//end footer

/* FOOTER BLOCKS
------------------- */
// copyright
.block-1{
	width: 100%;
	max-width: $siteMaxWidth;
	@include auto;
	.logged-in &{
		max-width: 100%;
	}
	p{
		text-align: right;
		font-size: 1.2rem;
		margin-bottom: 0;
		@include tab{
			font-size: 1.4rem;
		}
	}
}